import React,{useContext} from 'react';
import Select from "react-select"
import styled from "styled-components"
import { LanguageContext } from '../Context/LanguageContext';

const Looking = () => {
    const {rtl} = useContext(LanguageContext);
    const options1 = [
        { value: 'chocolate', label: 'Dubai Arab Emirates' },
        { value: 'strawberry', label: 'Strawberry' },
        { value: 'vanilla', label: 'Vanilla' }
      ];
    const options2 = [
        { value: 'red', label: 'Lorem Ipsum Is Simply' },
        { value: 'yellow', label: 'Yellow' },
        { value: 'green', label: 'Green' }
      ];
    const options3 = [
        { value: 'chocolate', label: 'Pizza' },
        { value: 'strawberry', label: 'Burger' },
        { value: 'cheeze', label: 'Food' }
      ];
    const options4 = [
        { value: 'option1', label: 'Pizza' },
        { value: 'option2', label: 'Burger' },
        { value: 'option3', label: 'Food' }
      ];

    return ( 
        <div className="bg-gray-200 p-5">
            <h2 className="text-center text-3xl font-bold mb-2">Looking For</h2>
            <div className="flex justify-center mb-4"><hr className ="border-t-4 border-solid border-red-700 rounded-full w-16"/></div>
            <div className={`flex flex-col items-center ${rtl ? "md:flex-row-reverse" :"md:flex-row"} md:justify-center md:flex-wrap`}>
                <button className={`bg-white text-black ${rtl ? "text-right md:rounded-r-lg" : "text-left md:rounded-l-lg"} w-64 h-16 m-1 rounded-lg font-bold px-2 md:rounded-none`}>Regiton
                    <SelectStyle language ={rtl} label="Select" classNamePrefix="MohtashimSelect" options={options1}
                       defaultValue={{label:"Select Any",value:""}} components={{ DropdownIndicator:() => <Arrow/> , IndicatorSeparator:() => null }}
                    />
                </button>
                <button className={`bg-white text-black ${rtl ? "text-right" : "text-left"} md:w-56 h-16 m-1 px-2 font-bold rounded-lg w-64 md:rounded-none`}>Highlights
                    <SelectStyle language={rtl} label="Select" classNamePrefix="MohtashimSelect" options={options2}
                       defaultValue={{label:"Select Any",value:""}} components={{ DropdownIndicator:() => <Arrow/> , IndicatorSeparator:() => null }}
                    />
                </button>
                <button className={`bg-white text-black ${rtl ? "text-right" : "text-left"} md:w-48 h-16 m-1 px-2 font-bold rounded-lg w-64 md:rounded-none`}>Speciality
                    <SelectStyle language={rtl} label="Select" classNamePrefix="MohtashimSelect" options={options3}
                       defaultValue={{label:"Select Any",value:""}} components={{ DropdownIndicator:() => <Arrow/> , IndicatorSeparator:() => null }}
                    />
                </button>
                <button className={`bg-white text-black text-left w-64 h-16 m-1  ${rtl ? "text-right md:rounded-l-lg ml-3" : "text-left md:rounded-r-lg mr-3"} md:rounded-none font-bold rounded-lg px-2`}>Keywords
                    <SelectStyle language={rtl} label="Select" classNamePrefix="MohtashimSelect" options={options4}
                       defaultValue={{label:"Select Any",value:""}} components={{ DropdownIndicator:() => <Arrow/> , IndicatorSeparator:() => null }}
                    />
                </button>
                <button className="bg-red-700 text-white text-center w-40 h-16 rounded-lg pl-1 m-1 font-bold">
                    <svg className="w-5 h-5 inline-block mr-2" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
                    </svg> 
                    Search
                </button>
            </div>
        </div>
     );
}
 
export default Looking;

const SelectStyle = styled(Select)`

    .MohtashimSelect__control {
        border: none;
        display: flex;
        justify-content: space-between;
        ${props => props.language ? "flex-direction:row-reverse" : "flex-direction:row"};
    }
    .MohtashimSelect__value-container{
        font-weight: normal;
        display: flex; 
        ${props => props.language && "justify-content: flex-end" };
    }

    .css-yk16xz-control{
        min-height: 0px;
    }
    .css-g1d714-ValueContainer{
        padding: 0px;
    }
    .css-1pahdxg-control{
        box-shadow: none;
        min-height: 0px;
    }
    .MohtashimSelect__menu{
        font-weight: normal;
        font-size: 15px;
        ${props => props.language ? "text-align-right" : "text-align: left"};
    }
`;

const Arrow = styled.div`
    border: solid black;
    border-width: 0 1px 1px 0;
    transform: rotate(45deg);
    height: 10px;
    width: 10px;
    margin-right: 20px;
    margin-left: 20px;
    margin-bottom: 10px;
`;