import React,{useContext, useState} from 'react';
import Logo from "../../Images/SmakenAsset-1-1 1.png";
import Location from "../../Images/location icon.png";
import Language from "../../Images/united-states flag.png";
import Search from "../../Images/search icon.png";
import styled from "styled-components";
import { LanguageContext } from '../Context/LanguageContext';
import { Link } from "react-router-dom";

const Header = () => {
    const {rtl, setRtl} = useContext(LanguageContext);
    const [click , setClick] = useState(false);

    const handleClick = () => {
        setClick(!click);
    }

    const handleLanguage = () => {
       rtl ? setRtl(false) : setRtl(true);
    }

    return ( 
        <Outer className="white xl:bg-black text-white mb-2 relative">
            <Wrapper className="flex xl:flex-row xl:items-center" click={click} language={rtl}>
                <First className={`flex flex-col items-center justify-evenly ${rtl ? "xl:order-last xl:flex-row-reverse" : "xl:order-first xl:flex-row" }  flex-1 xl:justify-around relative xl:flex-wrap`}>
                    <li className="block xl:inline-block mr-2 font-semibold text-base hover:text-gray-700 transition duration-500 ease-in-out">RESTAURANTS
                        <svg className="w-4 h-4 inline-block ml-2" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M19 9l-7 7-7-7" />
                        </svg>
                        <ul className="absolute bg-black z-10">
                            <li className="hover:text-white transition duration-500 ease-in-out font-semibold text-base"> Link 1 </li>
                            <li className="hover:text-white transition duration-500 ease-in-out font-semibold text-base"> Link 2 </li>
                            <li className="hover:text-white transition duration-500 ease-in-out font-semibold text-base"> Link 3 </li>
                        </ul>
                    </li>
                    <li className="inline-block mr-2 font-semibold text-base hover:text-gray-700 transition duration-500 ease-in-out">HOTELS</li>
                    <li className="inline-block mr-2 font-semibold text-base hover:text-gray-700 transition duration-500 ease-in-out">CAFE</li>
                    <li className="inline-block mr-2 font-semibold text-base hover:text-gray-700 transition duration-500 ease-in-out">HOSPITALS</li>
                </First>
                <ul className={`flex-1 flex items-center justify-center order-first xl:order-none`}>
                    <li className="cursor-pointer">
                        <Link to="/"><img src={Logo} alt="Amaken" className="w-40 my-3"/></Link>
                    </li>
                </ul>
                <Second className={`${rtl ? "xl:flex-row-reverse xl:order-first" :"xl:flex-row"} flex-1 flex flex-col xl:flex-wrap justify-evenly items-center xl:pb-0 pb-10`}>
                    <li className="flex flex-1 justify-center hover:text-gray-700 transition duration-500 ease-in-out font-semibold text-base mr-2">
                        <img src={Location} alt="Location" className="items-baseline h-5 w-3 inline-block mx-2"/>
                        Dubai-UAE
                    </li>
                    <li className="hover:text-gray-700 transition duration-500 ease-in-out flex-1 font-semibold text-base mr-2">
                        <img src={Language} alt="Language" className="w-5 inline-block mx-2 ml-5"/>
                        {rtl ? "Arabic" :"EN"}
                        <svg className="w-4 h-4 inline-block ml-2" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M19 9l-7 7-7-7" />
                        </svg>
                        <ul className="absolute bg-black z-10">
                            <li className="hover:text-white transition duration-500 ease-in-out font-semibold text-base" onClick={handleLanguage}>{rtl ? "EN" : "Arabic"}</li>
                        </ul>
                    </li>
                    <li className="flex-1 mr-2">
                        <button className="rounded-full border-gray-600 border-2 py-1 px-8 hover:text-gray-700 transition duration-500 ease-in-out font-semibold text-base">Login</button>
                    </li>
                    <li className={`flex-1 flex ${rtl && "justify-center"} mr-2`}>
                        <img src={Search} alt="Search" className="w-6 items-baseline h-6"/>
                    </li>
                </Second>
            </Wrapper>
            <svg className={`bg-white text-black block xl:hidden w-10 h-10 absolute z-10 ${rtl ? "right-0" : "left-0"}`} onClick={handleClick} xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M4 6h16M4 12h16M4 18h16" />
            </svg>
        </Outer>
     );
}
 
export default Header;

const Outer = styled.div`

    @media (max-width: 1279px){
        height: 10px;
    }
`;

const Wrapper = styled.div`
 
    @media (max-width: 1279px){   
        top:40px;
        position: fixed;
        flex-flow: column;
        align-items: center;
        height: 100vh;
        right: ${props => props.language && "0"};
        padding: 20px;
        transform: ${props => {
            if(props.click) {
                return "translateX(0%)"
            };
            if(props.language){
                return "translateX(125%)"
            }
            return "translateX(-125%)"
        }};
        transition: transform .5s;
        z-index: 100;
        background-color: black;
    }
`;

const First = styled.ul`
    & > li{
        font-family:Montserrat;
        & > ul {
            display:none;
        }

        :hover{
            & > ul {
                display: block;
            }
        }
    }
`;

const Second = styled.ul`
    & > li{
        font-family: Montserrat;
        & > ul {
            display:none;
        }

        :hover{
            & > ul {
                display: block;
            }
        }
    }
`;