import React, { Component } from "react";
import Carousel from "react-spring-3d-carousel";
import  { v4 as uuidv4}from "uuid";
import { config } from "react-spring";
import Div1Back from "../../Images/asset34.png"
import Div2Back from "../../Images/asset33.png"
import Div3Back from "../../Images/asset35.png"
import No from "../../Images/asset.png"
import Cook from "../../Images/asset12.png"
import Inner from "../../Images/asset9.png"
import Home from "../../Images/asset23.png"
import Location from "../../Images/asset25.png"
import Menu from "../../Images/asset30.png"
import News from "../../Images/asset31.png"
import Timing from "../../Images/asset56.png"
import Events from "../../Images/asset18.png"
import styled from "styled-components"
import { Link } from "react-router-dom";

export default class UpperSlider extends Component {
  state = {
    goToSlide: 0,
    config: config.gentle,
    i:0,
    slides:[
      {
        key: uuidv4(),
        content: 
        <div className="bg-opacity-25" style={{backgroundImage: `linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), url(${Div1Back})`, width:"780px", backgroundSize:"100% 100%", borderRadius: "30px"}}>
        <div className="flex justify-end h-16 pr-5 pt-5">
            <img src={No} alt="" className="w-16 h-10"/>
        </div>
        <div className="flex justify-center items-center mb-8">
            <div className="flex justify-center items-center rounded-full bg-white w-56 h-56">
                <img src={Cook} alt="" className="w-48 h-48"/>
            </div>
            <h1 className="text-white font-bold text-4xl ml-2">Rasoi Ghar <br/> Restaurant</h1>
        </div>
        <div className="flex justify-evenly mb-5" style={{fontFamily: "Montserrat"}}>
            <Link to="/home" className="text-white">
                <Buttons className="flex rounded-full w-16 h-16 justify-center items-center">
                    <img src={Home} alt="Home" className="w-6 h-6"/>
                </Buttons>
                HOME
            </Link>
            <Link to="/news" className="text-white">
                <Buttons className="flex rounded-full w-16 h-16 justify-center items-center">
                    <img src={News} alt="News" className="w-6 h-6"/>
                </Buttons>
                NEWS
            </Link>
            <Link to="/events" className="text-white">
                <Buttons className="flex rounded-full w-16 h-16 justify-center items-center">
                    <img src={Events} alt="Events" className="w-6 h-6"/>
                </Buttons>
                EVENTS
            </Link>
            <Link to="/timing" className="text-white">
                <Buttons className="flex rounded-full w-16 h-16 justify-center items-center">
                    <img src={Timing} alt="Timing" className="w-6 h-6"/>
                </Buttons>
                TIMING
            </Link>
            <Link to="/menu" className="text-white">
                <Buttons className="flex rounded-full w-16 h-16 justify-center items-center">
                    <img src={Menu} alt="Menu" className="w-6 h-6"/>
                </Buttons>
                MENU
            </Link>
            <Link to="/location" className="text-white">
                <Buttons className="flex rounded-full w-16 h-16 justify-center items-center">
                    <img src={Location} alt="Location" className="w-6 h-6"/>
                </Buttons>
                LOCATION
            </Link>
        </div>
    </div>
      },
      {
        key: uuidv4(),
        content: 
        <div style={{backgroundImage: `linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), url(${Div2Back})`, width:"780px", backgroundSize:"100% 100%", borderRadius:"30px"}}>
        <div className="flex justify-end h-16 pr-5 pt-5">
            <img src={No} alt="" className="w-16 h-10"/>
        </div>
        <div className="flex justify-center items-center mb-8">
            <div className="flex justify-center items-center rounded-full bg-white w-56 h-56">
                <img src={Cook} alt="" className="w-40 h-40"/>
            </div>
            <h1 className="text-white font-bold text-4xl ml-2">Rasoi Ghar <br/> Restaurant</h1>
        </div>
        <div className="flex justify-evenly mb-5" style={{fontFamily: "Montserrat"}}>
            <Link to="/home" className="text-white">
                <Buttons className="flex rounded-full w-16 h-16 justify-center items-center">
                    <img src={Home} alt="Home" className="w-6 h-6"/>
                </Buttons>
               HOME
            </Link>
            <Link to="/news" className="text-white">
                <Buttons className="flex rounded-full w-16 h-16 justify-center items-center">
                    <img src={News} alt="News" className="w-6 h-6"/>
                </Buttons>
                NEWS
            </Link>
            <Link to="/events" className="text-white">
                <Buttons className="flex rounded-full w-16 h-16 justify-center items-center">
                    <img src={Events} alt="Events" className="w-6 h-6"/>
                </Buttons>
                EVENTS
            </Link>
            <Link to="/timing" className="text-white">
                <Buttons className="flex rounded-full w-16 h-16 justify-center items-center">
                    <img src={Timing} alt="Timing" className="w-6 h-6"/>
                </Buttons>
                TIMING
            </Link>
            <Link to="/meenu" className="text-white">
                <Buttons className="flex rounded-full w-16 h-16 justify-center items-center">
                    <img src={Menu} alt="Menu" className="w-6 h-6"/>
                </Buttons>
                MENU
            </Link>
            <Link to="/location" className="text-white">
                <Buttons className="flex rounded-full w-16 h-16 justify-center items-center">
                    <img src={Location} alt="Location" className="w-6 h-6"/>
                </Buttons>
                LOCATION
            </Link>
        </div>
    </div>
      },
      {
        key: uuidv4(),
        content: 
        <div style={{backgroundImage: `linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)),url(${Div3Back})`, width:"780px", backgroundSize:"100% 100%", borderRadius:"30px"}}>
        <div className="flex justify-end h-16 pr-5 pt-5">
            <img src={No} alt="" className="w-16 h-10"/>
        </div>
        <div className="flex justify-center items-center mb-8">
            <div className="flex justify-center items-center rounded-full bg-white w-56 h-56">
                <img src={Inner} alt="" className="w-40 h-40"/>
            </div>
            <h1 className="text-white font-bold text-4xl ml-2">Rasoi Ghar <br/> Restaurant</h1>
        </div>
        <div className="flex justify-evenly mb-5" style={{fontFamily: "Montserrat"}}>
            <Link to="/home" className="text-white">
                <Buttons className="flex rounded-full w-16 h-16 justify-center items-center">
                    <img src={Home} alt="Home" className="w-8 h-8"/>
                </Buttons>
                HOME
            </Link>
            <Link to="/news" className="text-white">
                <Buttons className="flex rounded-full w-16 h-16 justify-center items-center">
                    <img src={News} alt="News" className="w-6 h-6"/>
                </Buttons>
                NEWS
            </Link>
            <Link to="/events" className="text-white">
                <Buttons className="flex rounded-full w-16 h-16 justify-center items-center">
                    <img src={Events} alt="Events" className="w-6 h-6"/>
                </Buttons>
                EVENTS
            </Link>
            <Link to="/timing" className="text-white">
                <Buttons className="flex rounded-full w-16 h-16 justify-center items-center">
                    <img src={Timing} alt="Timing" className="w-6 h-6"/>
                </Buttons>
                TIMING
            </Link>
            <Link to="/menu" className="text-white">
                <Buttons className="flex rounded-full w-16 h-16 justify-center items-center">
                    <img src={Menu} alt="Menu" className="w-6 h-6"/>
                </Buttons>
                MENU
            </Link>
            <Link to="/location" className="text-white">
                <Buttons className="flex rounded-full w-16 h-16 justify-center items-center">
                    <img src={Location} alt="Location" className="w-6 h-6"/>
                </Buttons>
                LOCATION
            </Link>
        </div>
    </div>
      },
    ].map((slide, index) => {
      return { ...slide, onClick: () => {
        this.setState({ goToSlide: index})
        clearInterval(this.interval);
        }};
    }),
  };

  Myfunction() {
    let Length = this.state.slides.length;
    this.setState({ goToSlide: this.state.i + 1});
      if(this.state.i == Length-1){
        this.setState({ goToSlide: 0});
        this.setState({i : -1});
      }
      this.setState({i : this.state.i + 1});
  }

  interval = setInterval(() => {
    this.Myfunction();
  }, 4000);


  render() {
    return (
        <div className="bg-white px-40 hidden md:block pb-10 z-0" style={{width:"100%", height:"530px"}}>
            <Carousel
                slides={this.state.slides}
                goToSlide={this.state.goToSlide}
                animationConfig={this.state.config}
            />
            <Slash>
            {
                this.state.slides.map((slide,index) => <BottomDot key={index} isActive={index === this.state.goToSlide} 
                onClick={() => {
                this.setState({goToSlide:index});
                clearInterval(this.interval);
            }}
            />)
          }
            </Slash>
        </div>
    );
  }
}

const Slash = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
`;

const BottomDot = styled.hr`
  width: ${props => props.isActive ? 30 : 15}px;
  border-top:${props => props.isActive ? "6px solid #c1272d" : "6px solid #3a3a3a" };
  border-radius: 10px;
  margin-right:2px;
  margin-left:2px;
`;

const Buttons = styled.div`
    background-color: rgba(0,0,0,0.7);
    :hover{
        background-color: gray;
    }
`;