import React from 'react';
import Calender from "../../Images/asset6.png"

const plc = [
    {
        url: `${Calender}`,
        text: "02-02-2021",
        heading: "Vestibulum. Felis Donec Et",
        content: "Iaculis eu non diam phasellus vestibulum. Felis donec et odio..."
    },
]
const Data = [...plc, ...plc, ...plc, ...plc];
const LatestNews = () => {
    return ( 
        <div className="bg-gray-100 flex-col flex p-4">
            <h1 className="font-semibold text-2xl mb-2">Latest News</h1>
            <div className="flex mb-4"><hr className ="border-t-4 border-solid border-red-700 rounded-full w-16"/></div>
            {
                Data.map(({url, text, heading, content}) => {
                    return (
                        <div>
                            <div className="flex mb-2">
                                <img src={url} alt="" className="h-6 w-6"/>
                                <time className="font-bold text-red-700 ml-2">{text}</time>
                            </div>
                            <div>
                                <h1 className="font-bold mb-2">{heading}</h1>
                            </div>
                            <div>
                                <p className="text-gray-600 mb-2">{content}</p>
                            </div>
                            <hr className="mb-4"/>
                        </div>
                    )
                })
            }
        </div>
     );
}
 
export default LatestNews;