import React from 'react';
import NewCard from '../Page 2 Components/Cards/EventListing';
import { Pagination } from '@material-ui/lab';
import Title from '../Reuseable Components/Title/Title';

const EventList = () => {
    return ( 
        <div>
            <Title/>
            <NewCard/>
            <div className="flex justify-center">
                <Pagination count={5} className="mt-2 text-center" shape="rounded" variant="outlined"/>
            </div>
        </div>
     );
}
 
export default EventList;